.employee {
    position: relative;
    width: 280px;
}

.employee .header {
    background-color: #F0F0F0;
    border-radius: 10px 10px 0px 0px;
}

.employee img {
    width: 100px;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.employee .info {
    background: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
}

.employee .info h4 {
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}

.employee .info h5 {
    font-size: 18px;
    line-height: 22px;
    color: #212121;
    padding: 0 16px;
}

.delete {
    position: absolute;
    right: -10px;
    top: -10px;
}

.delete:hover {
    cursor: pointer;
}

.favorite {
    margin-top: 20px;
}