.field {
    margin: 24px 0;
}

.field label {
    display: block;
    margin-bottom: 8px;
    font-size: 24px;
}

.field input {
    background-color: #FFF;
    box-shadow: 8px 8px 16px rgb(0, 0, 0, 0.08);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;
}

.field-color input {
    height: 40px;
    padding: 5px;
}